div .myTrips {
  color: black;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}



.leading-trip {
  background: rgba(255,255,255,0.6);
}

.myTripsBox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.mytrips-vehicle-req{
  display: flex;
  align-items: center;
  margin-bottom: 1.5em;
}
.mytrips-status-badge{
  margin-right: 1em;
}
.mytrips-req-header-and-status{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.mytrips-req-header{
  width: 80%;
  font-size: 1.4em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color:#0CA074;
}
.mytrips-req-header:hover{
  color:#0CA074;
}
.mytrips-request-button{
  width: 13em;
  height: 3em;
  margin-right: 1em;
  background: #0CA074;
  border-radius: 5px;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
.mytrips-request-button:hover{
  text-decoration: none;
  color: #ffffff;
  background: rgb(26, 179, 133);
}
.mytrips-request-and-calendar-links{
  margin-top: 1em;
  display: flex;
  align-items: center;
  margin-bottom: 1.5em;
}
.mytrips-calendar-link{
  color:#0CA074;
}
a.mytrips-calendar-link:hover{
  color:#0CA074;
}

.mytrips-vehicle-reqs{
  min-height: 7em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mytrips-req-status{
  text-transform: capitalize;
}
