@import './base.scss';

.create-trip-form {

  .description-box {
    min-height: 200px;
  }

  .description-list{
    font-size: 20px;
    line-height: 27px;
  }

  .create-trip-form-content {
    display: flex;
    flex-direction: column;

    .page-sub-headers {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      > * {
        margin-bottom: 1em;
      }
    }

    .trip-date-range-inputs {
      display: flex;
      margin-top: 1.5rem;

      .page-sub-headers {
        width: 50%;
      }
    }

    &.pcard{
      display: flex;
      width: 70%;
      align-items: center;
      justify-content: space-between;
    }

    .create-trip-form-bottom-margin {
      margin: 0rem 0rem 3rem 0rem;
    }
  }
}

#date-picker{
  display: -webkit-inline-box;
}
input[type="date"]{
  width: 18.6rem;
  margin-right: 4rem;
}
input[type="time"]{
  margin-right: 4rem;
}

.add-gear-button{
  width: 50%;
  height: 3.5rem;
  border: 3px solid #0CA074;
  box-sizing: border-box;
  border-radius: 5px;
  margin-top: 3rem;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 800;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 0.05em;
  color: #0CA074;
}

.gear-container {

  .gear-and-size {
    display: flex;
    flex-direction: column;

    > * {
      margin-bottom: 1em;
    }
  }

  .remove-gear-button {
    margin-left: 1em;
  }

  .delete-gear-button {
    content: "X";
    display: inline-block;
    width: 1rem;
    height: 4rem;
    background-color: transparent;
    z-index:1;
    margin: auto;
    padding: 2px;
    text-align: center;
    color: #979797;
    font-weight: normal;
    font-size: 20px;
    border-color: transparent;
    border-width: 2px;
    border-left: 0px;
    cursor: pointer;
    &:focus{
      outline: none;
      border-color: transparent;
    }
  }
}



