  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 0.05em;
  color: #0CA074;
}

.checkbox-beginner input[type="checkbox"]{
    opacity: 0;
}

.checkbox-beginner label {
    position: relative;
    display: inline-block;

    /*16px width of fake checkbox + 6px distance between fake checkbox and text*/
    padding-left: 22px;
}

.checkbox-beginner label::before,
.checkbox-beginner label::after {
    margin-top: .3rem;
    position: absolute;
    content: "";

    /*Needed for the line-height to take effect*/
    display: inline-block;
}

/*Outer box of the fake checkbox*/
.checkbox-beginner label::before{
    height: 16px;
    width: 16px;
    border: 2px solid #0CA074;
    left: 0px;

    /*(24px line-height - 16px height of fake checkbox) / 2 - 1px for the border
     *to vertically center it.
     */
    top: 3px;
}

/*Checkmark of the fake checkbox*/
.checkbox-beginner label::after {
    height: 5px;
    width: 9px;
    border-left: 2px solid white;
    border-bottom: 2px solid white;
    transform: rotate(-45deg);
    left: 4px;
    top: 7px;
}

/*Hide the checkmark by default*/
.checkbox-beginner input[type="checkbox"] + label::after {
    content: none;
}

/*Unhide on the checked state*/
.checkbox-beginner input[type="checkbox"]:checked + label::after {
    content: "";
}
.checkbox-beginner input[type="checkbox"]:checked + label::before{
    content: "";
    border: wpx solid #0CA074;
    background-color: #0CA074;
}

