ul li .radio-button{
  display: block;
  position: absolute;
  border: 3px solid #939393;
  border-radius: 100%;
  height: 35px;
  width: 35px;
  left: 1.5%;
  top: 61%;
	z-index: 5;
	transition: border .25s linear;
	-webkit-transition: border .25s linear;
}

.my-row{
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  flex-direction: row;
  align-items: stretch;
  flex-shrink: 0;
  min-height: 100%;
  justify-content: flex-start;
}

.left-column{
  background: white;
  width: 20%;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: fixed; /* Fixed Sidebar (stay in place on scroll) */
  z-index: 1; /* Stay on top */
  overflow-x: hidden;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
  padding-right: 0.5em;
  padding-top: 1em;
}

.side-bar-highlight{
  position: absolute;
  width: 0.5em;
  height: 2.5rem;
  left: 0.1rem;
  background: #0CA074;
  border-radius: 0px 5px 5px 0px
}

.right-column{
  margin-left: 30%;
  padding-top: 1em;
  padding-bottom: 1em;
  padding-left: 2.5em;
  padding-right: 2.5em;
  width: 80%;
  min-height: 100%;
}

.column-headers{

  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 800;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 0.05em;
  color: #000000;
  margin: 1rem 0rem 1rem 2rem;
  flex: 0 1 content;
}

.column-adjust{
